.header-bar {
    display: flex;
    justify-content: space-between;
    z-index: 1;
    font-size: 20px;
    height: 40px;
    background-color: #2f3847;
    padding: 10px;
}

.header-left {
    display: flex;
    flex-direction: row;
    width: 20%;
    background-color: blueviolet;
}

.header-center {
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 30px;
    width: 30%;
    color: #e6eaff;
}

.header-right {
    display: flex;
    width: 20%;
    flex-direction: row-reverse;
    align-items: center;
    color: #e6eaff;
}

